@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100vh;
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: 100%;
}

.container {
  min-width: 280px;
  margin: 0 auto;
  padding: 0 20px 0 20px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}

.container {
  max-width: 280px;
  margin: 0 auto;
  padding: 0 20px 0 20px;
}

.button {
  min-width: 280px;
  min-height: 40px;
  font-family: Oswald;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  background: #68b738;
}

.functional {
  margin-bottom: 20px;
}

.functional__container {
  min-width: 320px;
  max-height: 302px;
  text-align: center;
  color: #ffffff;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: url("../img/hero-cat.png");
  background-repeat: no-repeat;
}

.functional__title {
  min-width: 249px;
  min-height: 72px;
  margin: 0;
  margin-bottom: 25px;
  padding: 27px 35px 0 36px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  font-family: Oswald;
  text-align: center;
}

.functional__text {
  min-width: 198px;
  min-height: 14px;
  margin: 0;
  margin-bottom: 29px;
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  font-family: Oswald;
  text-transform: uppercase;
}

.functional__image {
  width: 280px;
  height: 270px;
  margin-bottom: 3px;
}

.functional__button {
  padding: 10px 44px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: Oswald;
  text-align: center;
  text-transform: uppercase;
}

